<template>
	<v-btn
		depressed
		color="cyan"
		@click="exportWord"
		id="my-canvas"
		dark>
		<v-icon class="mr-2">mdi-file-word</v-icon>
		export to word
	</v-btn>
</template>

<script>
import { saveAs } from 'file-saver';
import { 
	AlignmentType, Document, 
	Packer, UnderlineType,
	Paragraph, TextRun, HeadingLevel
} from "docx";

  export default{
    // data: () => ({
    //   state: {
    //     name: 'San Luis Potosi',
    //     map: 'data:image/png;base64',
    //     municipalities: [
    //       {name:'San Luis Potosi', population: 824000}, 
    //       {name:'Rio Verde', population: 160000},
    //       {name:'Cd Valles', population: 176000},
    //       {name:'Matehuala', population:82726}
    //     ],
    //     tourist_attractions: [
    //       'Tamtoc', 'Stano de las Golondrinas', 'Cascada de Tamul' 
    //     ]
    //   }
    // }),

    methods: {
      exportWord() {
      const doc = new Document({
        creator: "Progresio",
        title: "Progres Sprint",
        description: "Progress sprint startup setiap minggu",
        styles: {
          paragraphStyles: [
              {
                  id: "Heading1",
                  name: "Heading 1",
                  basedOn: "Normal",
                  next: "Normal",
                  quickFormat: true,
                  run: {
                      size: 28,
                      bold: true,
                      italics: true,
                      color: "red",
                  },
                  paragraph: {
                      spacing: {
                          after: 120,
                      },
                  },
              },
              {
                  id: "Heading2",
                  name: "Heading 2",
                  basedOn: "Normal",
                  next: "Normal",
                  quickFormat: true,
                  run: {
                      size: 26,
                      bold: true,
                      underline: {
                          type: UnderlineType.DOUBLE,
                          color: "FF0000",
                      },
                  },
                  paragraph: {
                      spacing: {
                          before: 240,
                          after: 120,
                      },
                  },
              },
              {
                  id: "aside",
                  name: "Aside",
                  basedOn: "Normal",
                  next: "Normal",
                  run: {
                      color: "999999",
                      italics: true,
                  },
                  paragraph: {
                      indent: {
                          left: 720,
                      },
                      spacing: {
                          line: 276,
                      },
                  },
              },
              {
                  id: "wellSpaced",
                  name: "Well Spaced",
                  basedOn: "Normal",
                  quickFormat: true,
                  paragraph: {
                      spacing: { line: 276, before: 20 * 72 * 0.1, after: 20 * 72 * 0.05 },
                  },
              },
              {
                  id: "ListParagraph",
                  name: "List Paragraph",
                  basedOn: "Normal",
                  quickFormat: true,
              },
          ],
        },
        numbering: {
          config: [
            {
              reference: "my-crazy-numbering",
              levels: [
                  {
                      level: 0,
                      format: "decimal",
                      text: "%1.",
                      alignment: AlignmentType.LEFT,
                  },
              ],
            },
          ],
        },
      });

      doc.addSection({
        children: [
          new Paragraph({
              text: "LOG PENDAMPINGAN",
              heading: HeadingLevel.HEADING_1,
          }),
          new Paragraph({
            children: [
              new TextRun(`NAMA TENANT\t: Smartedu`).break(),
              new TextRun(`CEO\t\t\t: Mr. X`).break(),
              new TextRun(`SPRINT\t\t: 8`).break(),
              new TextRun(`TANGGAL\t\t: 24 November 2020`).break()
            ]
          }),
					new Paragraph({
						text: 'REVIEW DARI SPRINT SEBELUMNYA',
						alignment: AlignmentType.CENTER
					}),
          new Paragraph({
            text: "Option1",
            numbering: {
              reference: "my-crazy-numbering",
              level: 0,
            },
          }),  
          new Paragraph({
            children: [
              new TextRun(`Yogyakarta, 24 September 2019`).break(),
              new TextRun(`Tenant\t\t\t\t\t\t\t\t\t\t\t\t\tPendamping`).break().break().break(),
              new TextRun(`Nama tenant\t\t\t\t\t\t\t\t\t\t\t\tNama incubator`).break().break().break().break().break()
            ]
          }),        
        ],
      });

				// Used to export the file into a .docx file
				Packer.toBlob(doc).then(blob => {
          saveAs(blob, "detailed_report.docx");
				});

      }
    }
  }
</script>