<template>
  <v-dialog
    v-model="dialog"
    width="800">
    <template v-slot:activator="{ on }">
      <v-icon v-on="on">mdi-dots-horizontal</v-icon>
    </template>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text class="black--text">
        <center>
          <div v-if="cekFile(detailCard.berkas) === ''"></div>                 
          <div v-if="cekFile(detailCard.berkas) === 'gambar'" >
            <img              
              :src="`http://progresio.id/api/public/uploads/berkas/${detailCard.berkas}`" 
              width="250" 
            /> <br>
            <v-btn
              text
              small
              class="ml-3"
              color="primary">
              download
            </v-btn>
          </div>  
          <div 
            v-if="cekFile(detailCard.berkas) === 'file'"
            class="d-flex align-center justify-center my-4">
            <v-icon class="mr-3">mdi-file</v-icon>
            <span>{{ detailCard.berkas }}</span>
            <v-btn
              text
              small
              class="ml-3"
              color="primary">
              download
            </v-btn>
          </div> 
        </center>
        <v-row>
          <v-col cols="12" md="8" sm="12">
            <v-text-field
              prepend-icon="mdi-card-text"
              v-model="detailCard.title"
              :append-icon="editName?'mdi-pencil':''"
              @click:append="updateTitle"
              @focus="editName = true"
              @blur="editName = false"
              dense
              class="title font-weight-bold"
            ></v-text-field>
            <div class="d-flex">
              <v-icon>mdi-text</v-icon>
              <div class="title font-weight-bold ml-3">
                Deskripsi
              </div>
            </div>            
            <v-textarea
              class="mt-2 ml-9"
              outlined
              full-width
              v-model="detailCard.description"
              @focus="editDesc = true"
              @blur="editDesc = false"
              :append-icon="editDesc?'mdi-pencil':''"
              @click:append="updateDesc"
            ></v-textarea>            
            <div class="d-flex">
              <v-icon>mdi-attachment</v-icon>
              <div class="title font-weight-bold ml-3">
                Attachment
              </div>
            </div>             
            <v-file-input
              show-size
              counter
              label="File"
              class="ml-9"
              prepend-icon=""
              v-model="file"
              @focus="editFile = true"
              @blur="editFile = false"
              :append-icon="editFile?'mdi-pencil':''"
              @click:append="updateFile"
            ></v-file-input> 
          </v-col>
          <v-col cols="12" md="4" sm="12">
            <component :is="tabs" :id="data.id" :column_id="column_id" @actions="changeComponent" @dialog="closeDialog"></component>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
  import {
    putDataCardFile,
    putDataCardName,
    putDataCardDesc,
    getDetailCard
  } from '@/config/startup/sprint'
  import Copy from './Copy'
  import Move from './Move'
  import Actions from './Actions'

  export default {
    props: ['data', 'column_id'],

    data () {
      return {
        dialog: false,
        valid: true,
        editName: false,
        editDesc: false,
        editFile: false,
        file: null,
        detailCard: {},
        tabs: 'Actions'
      }
    },

    components: {
      Actions,
      Move,
      Copy
    },

    watch: {
      dialog(val) {
        if (val === true) {
          getDetailCard(this.data.id)
            .then(res => this.detailCard = res.data.data[0])
        }
      }
    },

    methods: {      
      cekFile(val) {
        if (val == null) {
          return ''
        }
        const file = val.split('.')
        const jenisFile = file[1]
        if (jenisFile === 'jpeg') {
          return 'gambar'
        } else {
          return 'file'
        }
      },
      changeComponent(val) {
        this.tabs = val
      },
      closeDialog(val) {
        this.dialog = val
      },
      updateFile () {
        const data = new FormData()
        data.append('berkas', this.file)
        putDataCardFile(this.data.id, data)    
          .then(() => this.$store.dispatch('dataStartupSprint/getDataList', this.$route.params.sprint_id))
      },
      updateTitle () {
        putDataCardName(this.data.id, {
          title: this.detailCard.title
        })
      },
      updateDesc () {
        putDataCardDesc(this.data.id, {
          description: this.detailCard.description
        })
      }
    }
  }
</script>